"use client";

import dynamic from 'next/dynamic';
import type { ComponentType } from 'react';

type QrScannerProps = {
  delay?: number;
  onError?: (error: any) => void;
  onScan?: (data: string | null) => void;
  style?: React.CSSProperties;
};

const QrScanner = dynamic<ComponentType<QrScannerProps>>(
  async () => {
    const mod = await import('react-qr-scanner');
    return mod.default; // ‚Üê „Åì„Åì„Åß default export „ÇíÊòéÁ§∫„Åó„Å¶Ëøî„Åô
  },
  { ssr: false }
);

export default function POSPage() {
  const [code, setCode] = useState("");
  const [product, setProduct] = useState(null);
  const [error, setError] = useState("");
  const [list, setList] = useState([]);
  const [quantities, setQuantities] = useState({});

  const handleRead = async (inputCode?: string) => {
    const targetCode = inputCode || code;
    if (!targetCode || targetCode.trim() === "") return;
  
    try {
      const res = await fetch(`http://localhost:8000/product?code=${targetCode}`);
      if (!res.ok) throw new Error("ÂïÜÂìÅ„ÅåË¶ã„Å§„Åã„Çä„Åæ„Åõ„Çì");
  
      const data = await res.json();
      setProduct(data);
      setError("");
    } catch (err) {
      console.error("‚ùå Ê§úÁ¥¢„Ç®„É©„Éº:", err);
      setProduct(null);
      setError("ÂïÜÂìÅ„Éû„Çπ„ÇøÊú™ÁôªÈå≤„Åß„Åô");
    }
  };
  
  
  
  const handleAdd = () => {
    if (product) {
      const existing = list.find((item) => item.CODE === product.CODE);
      if (!existing) {
        setList([...list, product]);
        setQuantities({ ...quantities, [product.CODE]: 1 });
      } else {
        setQuantities({
          ...quantities,
          [product.CODE]: quantities[product.CODE] + 1,
        });
      }
      setProduct(null);
      setCode("");
    }
  };

  const handleQuantity = (code: string, delta: number) => {
    const newQty = (quantities[code] || 0) + delta;
    if (newQty <= 0) {
      setList(list.filter((item) => item.CODE !== code));
      const { [code]: _, ...rest } = quantities;
      setQuantities(rest);
    } else {
      setQuantities({ ...quantities, [code]: newQty });
    }
  };

  const handleRemove = (code: string) => {
    setList(list.filter((item) => item.CODE !== code));
    const { [code]: _, ...rest } = quantities;
    setQuantities(rest);
  };

  const handlePurchase = async () => {
    const payload = {
      emp_cd: "9999999999",
      products: list.flatMap((item) => Array(quantities[item.CODE]).fill(item)),
    };
    const res = await fetch("http://localhost:8000/purchase", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(payload),
    });
    const data = await res.json();
    alert(`üßæ „ÅîÊ≥®Êñá„ÅÇ„Çä„Åå„Å®„ÅÜ„Åî„Åñ„ÅÑ„Åæ„Åó„ÅüÔºÅ\nÂêàË®àÈáëÈ°ç: Ôø•${data.total_amount} ÂÜÜ`);
    setList([]);
    setQuantities({});
  };

  const total = list.reduce(
    (sum, item) => sum + item.PRICE * (quantities[item.CODE] || 0),
    0
  );

  return (
    <div className="min-h-screen bg-gray-900 text-white p-6 max-w-xl mx-auto space-y-6">
      <h1 className="text-2xl font-bold text-center">POS„É¨„Ç∏„Ç∑„Çπ„ÉÜ„É†</h1>

      <div className="bg-gray-800 p-4 rounded shadow space-y-3">
        <label className="block font-semibold">QR„Ç≥„Éº„Éâ„Çπ„Ç≠„É£„É≥</label>
        <div className="w-full h-52 bg-black rounded overflow-hidden">
        <QrScanner
        delay={300}
        style={{ width: '100%', height: '100%' }}
        onScan={(data) => {
        if (data) setCode(data);
        }}
        onError={(err) => console.error('QR Scan Error', err)}
        />



        </div>
      </div>

      {error && <p className="text-red-400">{error}</p>}

      {product && (
        <div className="bg-gray-800 p-4 rounded shadow space-y-2">
          <div className="text-lg font-semibold">ÂïÜÂìÅÊÉÖÂ†±</div>
          <div>ÂêçÁß∞: {product.NAME}</div>
          <div>‰æ°Ê†º: Ôø•{product.PRICE}</div>
          <button
            onClick={handleAdd}
            className="bg-green-500 hover:bg-green-600 text-white font-bold py-2 px-4 rounded w-full mt-2"
          >
            „Ç´„Ç¥„Å´ËøΩÂä†
          </button>
        </div>
      )}

      <div className="bg-gray-800 p-4 rounded shadow space-y-4">
        <h2 className="font-bold text-lg">Ë≤∑„ÅÑÁâ©„Ç´„Ç¥</h2>
        {list.map((item) => (
          <div
            key={item.CODE}
            className="bg-gray-700 p-3 rounded flex flex-col space-y-2 text-sm"
          >
            <div className="font-semibold text-base">{item.NAME}</div>
            <div className="flex justify-between items-center">
              <div className="flex items-center space-x-2">
                <span>Êï∞Èáè:</span>
                <button
                  onClick={() => handleQuantity(item.CODE, -1)}
                  className="bg-red-500 hover:bg-red-600 text-white rounded-full w-6 h-6"
                >
                  ‚àí
                </button>
                <span className="font-bold text-white">{quantities[item.CODE] || 0}</span>
                <button
                  onClick={() => handleQuantity(item.CODE, 1)}
                  className="bg-green-500 hover:bg-green-600 text-white rounded-full w-6 h-6"
                >
                  Ôºã
                </button>
              </div>
              <div>Âçò‰æ°: Ôø•{item.PRICE}</div>
              <button
                onClick={() => handleRemove(item.CODE)}
                className="text-red-500 font-bold text-sm ml-2"
                title="„Ç´„Ç¥„Åã„ÇâÂâäÈô§"
              >
                ÂâäÈô§
              </button>
            </div>
            <div className="text-right font-semibold">
              Â∞èË®à: Ôø•{item.PRICE * (quantities[item.CODE] || 0)}
            </div>
          </div>
        ))}
      </div>

      <div className="bg-gray-800 p-4 rounded shadow text-center">
        <h2 className="text-lg font-bold mb-2">„ÅäÊîØÊâïÈ°ç</h2>
        <p className="text-2xl text-red-500">Ôø•{total}</p>
      </div>

      <button
        onClick={handlePurchase}
        disabled={list.length === 0}
        className="bg-blue-600 hover:bg-blue-700 text-white py-3 px-4 rounded w-full text-lg font-bold"
      >
        Ê≥®Êñá
      </button>
    </div>
  );
}
